* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #1a1a1a;
  background-color: #fefefe;
  font-family: Inter, Helvetica Neue, Arial, sans-serif;
  line-height: 1.6;
}

.portfolio {
  display: flex;
}

a {
  color: #0070f3;
  text-decoration: none;
  transition: color .3s;
}

a:hover {
  color: #0051a3;
}

.container {
  min-height: 100vh;
  display: flex;
}

.sidebar {
  text-align: center;
  background-color: #fff;
  border-right: 1px solid #e0e0e0;
  width: 250px;
  padding: 40px 20px;
  position: sticky;
  top: 0;
}

.sidebar .headshot {
  object-fit: cover;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin-bottom: 20px;
}

.sidebar h1 {
  margin-bottom: 5px;
  font-size: 1.8rem;
}

.sidebar p {
  color: #555;
  margin-bottom: 30px;
  font-size: .95rem;
}

.sidebar nav ul {
  flex-direction: column;
  gap: 15px;
  list-style: none;
  display: flex;
}

.sidebar nav ul li a {
  font-size: 1rem;
  font-weight: 500;
}

.content {
  flex: 1;
  max-width: 900px;
  margin: 0 auto;
  padding: 60px 40px;
}

section {
  margin-bottom: 60px;
}

section h2 {
  color: #1a1a1a;
  border-bottom: 2px solid #0070f3;
  margin-bottom: 20px;
  padding-bottom: 5px;
  font-size: 2rem;
  display: inline-block;
}

section p, section ul {
  color: #333;
  margin-bottom: 20px;
  font-size: 1rem;
}

section ul {
  padding-left: 20px;
}

section ul li {
  margin-bottom: 10px;
}

article {
  margin-bottom: 40px;
}

article h3 {
  color: #0070f3;
  margin-bottom: 5px;
  font-size: 1.3rem;
}

article em {
  color: #555;
  font-size: .9rem;
}

#projects {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.project-card {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  width: 280px;
  padding: 20px;
  transition: transform .3s, box-shadow .3s;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #00000014;
}

.project-card h3 {
  color: #0070f3;
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.project-card p {
  color: #333;
  font-size: .95rem;
}

footer {
  text-align: center;
  color: #777;
  border-top: 1px solid #e0e0e0;
  padding: 30px 0;
  font-size: .9rem;
}

@media (width <= 1024px) {
  .container {
    flex-direction: column;
  }

  .sidebar {
    border-bottom: 1px solid #e0e0e0;
    border-right: none;
    width: 100%;
    padding: 20px;
  }

  .content {
    padding: 40px 20px;
  }

  #projects {
    justify-content: center;
  }
}
